function tabs(tabsSelector, tabsContentSelector, tabsParentSelector, activClass) {
	// Tabs
	const tabs = document.querySelectorAll(tabsSelector),
	tabsContent = document.querySelectorAll(tabsContentSelector),
	tabsParent = document.querySelector(tabsParentSelector);

	function hideTabContent(){
		tabsContent.forEach(item => {
			item.classList.add('hide');
		item.classList.remove('is-show', 'fade');
		
		});

		tabs.forEach(item => { 
			item.classList.remove(activClass);
		});
	} 

	function showTabContent(i = 0) {
		tabsContent[i].classList.add('is-show', 'fade');
		tabsContent[i].classList.remove('hide');
		tabs[i].classList.add(activClass);
	}

	hideTabContent();
	showTabContent();

	tabsParent.addEventListener('click', (event) => {
		const target = event.target;

		if (target && target.classList.contains(tabsSelector.slice(1))){
			tabs.forEach((item, i) => {
				if( target == item) {
					hideTabContent();
					showTabContent(i);
				}
			});
		}})
	
}

export default tabs;